<template>
  <div class="common-info">
    <div class="topTitle">
      <span class="topTitle-span1">客户视图</span>
      <span class="topTitle-span1">
        <img src="@/assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">资金流水</span>
    </div>
    <div class="contentCellBox">
       <div class="contentCellBox">
          <div class="contentCellTitle">
            <div class="contentCellTitle-1"></div>查询条件
            <div class="button-content">
            </div>
          </div>
          <div>
            <Form
              ref="formValidate"
              :model="queryObject"
              :rules="ruleValidate"
              :label-width="120"
              inline
            >
              <Row>
                <FormItem label="查询日期：">
                    <DatePicker split-panels type="daterange" :value="date1" :options="options1" @on-change="updateDate1"></DatePicker>
                </FormItem>
                <FormItem label="业务类型：">
                    <Select v-model="queryObject.busiType" filterable style="width:200px">
                      <Option value="0">OTC</Option>
                      <Option value="1">零售</Option>
                      <Option value="2">理财</Option>
                    </Select>
                </FormItem>
                <FormItem label="证件号码" prop="credential">
                  <Input v-model="queryObject.credential"></Input>
                </FormItem>
                <FormItem label="委托类型：">
                    <Select v-model="queryObject.entrustType" filterable style="width:200px">
                      <Option v-for="item in entrustTypes" :value="item.code" :key="item.code">{{item.name}}</Option>
                    </Select>
                </FormItem>
              </Row>
              <Row>
                <div class="btnClass">
                  <Button type="primary" @click="handleSubmit()" style="width:89px">查询</Button>
                </div>
              </Row>
            </Form>
          </div>
      </div>
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td>序号</td>
                  <td v-for="(value,key) in tableHeader" :key="key">{{value}}</td>
                </tr>
              </thead>
              <tbody>
                  <tr v-for="(item,index) in tableBody" :key="index">
                    <td>{{queryObject.page.limit*(queryObject.page.start-1)+index+1}}</td>
                    <template v-for="(value,key) in tableHeader">
                      <td>{{item[key]}}</td>
                    </template>
                  </tr>
              </tbody>
            </table>
          </Row>
        </div>
        <div class="flexCenter pageBox">
          <Page :current.sync="queryObject.page.start" :total="rowCount" :page-size="queryObject.page.limit" @on-change="pageChange" @on-page-size-change='pageSizefun' show-elevator show-sizer/>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  created() {
      this.axios
      .get("/system/getDic/entrustWay")
      .then(response => {
        if (response.code == "200") {
          this.entrustTypes = response.data.entrustWay;
        }
      })
      .catch(function(error) {});
      let data = JSON.parse(JSON.stringify(this.queryObject));
      data = JSON.stringify(data);
      this.axios
        .post("/client/patch/list", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code=="200"){
                this.rowCount = response.data.total;
                this.tableBody = response.data.records;
          }
        })
        .catch(error => {});
  },
  data() {
    return {
      date1: ["",""],
      entrustTypes: [],
      //
      tableHeader: {
        "transactionDate":"交易日期",
        "CurrentDate": "当前日期",
        "currentTime":"当前时间",
        "SeriarNumber":"流水序号",
        "branches":"分支机构",
        "assetAccount":"资产账户",
        "customerNumber":"客户编号",
        "transactionType":"交易类别",
        "customerName":"客户姓名",
        "currencyCategory":"币种类别",
        "businessSign":"业务标志",
        "operationalBranch":"操作分支机构",
        "operatorNumber":"操作员编号",
        "siteAddress":"站点地址",
        "commissionMethod":"委托方式",
        "bankCode":"银行代码",
        "incurredAmount":"double",
        "postFunding":"后资金额",
        "associatedDate":"关联日期",
        "associatedSerialNumber":"关联流水号",
        "bankSerialNumber":"银行流水号",
        "freezeCategory":"冻结类别",
        "flowAuxiliaryInformation":"流水辅助信息",
        "remark":"备注",
        "positioningString":"定位串",
        "processingFlag":"处理标志",
        "reversalSerialNumber":"冲销流水号",
        "customerClassification":"客户分类",
        "customerGroup":"客户分组",
        "restrictionSign":"限制标志",
        "assetAttributes":"资产属性",
        "riskLevel":"风险级别",
        "companyCustomerType":"公司客户类型",
        "customerRiskLevel":"客户风险等级",
        "assetClass":"资产等级"
      },
       tableBody: [
      ],
      rowCount: 0,
      queryObject: {
        cilentId: this.$store.state.clientId,
        begindate: "",
        enddate: "",
        tradeType: "2",
        busiType: "0",
        credential: "",
        page: {
          limit: 10,
          start: 1
        },

      },
      options1: {
          disabledDate (date) {
              return date && date.valueOf() > Date.now()-24*60*60*1000;
          }
      },
      ruleValidate: {
          busiType: [
            {
              required: true,
              message: "业务类型不能为空！",
              trigger: "change"
            }
        ],
       }
    };
  },
  methods: {
    updateDate1(daterange) {
      this.date1 = daterange;
      this.queryObject.begindate = daterange[0];
      this.queryObject.enddate = daterange[1];
    },
    pageSizefun(a){
      this.queryObject.page.limit = a;
      if (this.queryObject.page.start == 1){
          this.pageChange(1);
      }
    },
    pageChange(pageNo) {
      this.queryObject.page.start = pageNo;
      // let valid = "";
      // this.$refs["formValidate"].validate(valid1 => {
      //   valid = valid1;
      // })
      // if (!valid) return;
      let data = JSON.parse(JSON.stringify(this.queryObject));
      data = JSON.stringify(data);
      this.axios
        .post("/client/patch/list", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code=="200"){
                this.rowCount = response.data.total;
                this.tableBody = response.data.records;
          }
        })
        .catch(error => {});
    },
    handleSubmit() {
      // let valid = "";
      // this.$refs["formValidate"].validate(valid1 => {
      //   valid = valid1;
      // })
      // if (!valid) return;
      let data = JSON.parse(JSON.stringify(this.queryObject));
      data = JSON.stringify(data);
      this.axios
        .post("/client/patch/list", data, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code=="200"){
                this.rowCount = response.data.total;
                this.tableBody = response.data.records;
          }
        })
        .catch(error => {});
    },
  },
  watch: {}
};
</script>
<style lang="scss">
</style>


